<template>
  <div class="page">
    <index-top
      :h1="'KISwap'"
      :h4="$t('index.最具潜力的集DEX、IKO、DAO、聚合交易为一体的DEFI生态平台')"
    ></index-top>
    <div class="reward-info flex-center">
      <div class="text">
        {{ $t("trading.当前池子总奖励：")
        }}<span class="second-color">{{ totalKisAmount }} KIS</span>
      </div>
      <div class="text">
        {{ $t("trading.当前个人可提现奖励：")
        }}<span class="second-color">{{ userTotalAmount }}KIS</span>
      </div>
    </div>
    <div class="add-btn" v-if="userTotalAmount > 0" @click="takerWithdraw">
      {{ $t("trading.提现奖励") }}
    </div>
    <div class="add-btn disabled" v-else>{{ $t("trading.提现奖励") }}</div>
    <div class="reward-pro flex-wrap ">
      <div class="pro-item" v-for="item in poolPairList" @click="toSwap(item)">
        <span class="icon-sign"
          >{{
            totalAllocPoint
              ? Number((item.allocPoint * 100) / totalAllocPoint).toFixed(2)
              : 0.0
          }}%</span
        >
        <div class="pro-top flex-center">
          <img :src="$tokenLogo(item.tokenA)" alt="" class="icon-logo" />
          <img
            src="@/assets/image/light/trading/tra.png"
            alt=""
            class="icon-tra"
            v-if="themeMode === 'light'"
          />
          <img
            src="@/assets/image/trading/tra.png"
            alt=""
            class="icon-tra"
            v-else
          />
          <img :src="$tokenLogo(item.tokenB)" alt="" class="icon-logo" />
        </div>
        <div class="pro-tit">{{ item.symbol }}</div>
        <div class="pro-info">
          <div class="row flex-middle">
            <div class="label">{{ $t("trading.收益率（日/年）") }}*:</div>
            <!-- {{rate && totalAllocPoint ? Number(item.allocPoint * totalMintDay / totalAllocPoint / 28800 / rate).toFixed(2) : 0}}%/ -->
            <div class="flex-item text-r cont">
              {{
                rate && totalAllocPoint
                  ? Number(
                      (item.allocPoint * totalMintDay * 365) /
                        totalAllocPoint /
                        28800 /
                        rate
                    ).toFixed(2)
                  : 0
              }}%
            </div>
          </div>
          <div class="row flex-middle">
            <div class="label">
              {{ $t("trading.当前总奖励") }}:
              <el-popover
                placement="right"
                :width="200"
                trigger="hover"
                :content="$t('trading.当前该池子可分配的总奖励')"
              >
                <template #reference><span class="icon-pop"></span></template>
              </el-popover>
            </div>
            <div class="flex-item text-r cont">
              {{ Number(item.kisTotal).toFixed(3) }}
            </div>
          </div>
          <!-- <div class="row flex-middle">
            <div class="label">{{$t('trading.交易预算')}}:
              <el-popover
                placement="right"
                width="200"
                trigger="hover"
                :content="$t('trading.当前该池子可分配的总奖励')">
                <template #reference><span class="icon-pop"></span></template>
              </el-popover>
            </div>
            <div class="flex-item text-r cont">$7,376,001,569.78</div>
          </div> -->
          <div class="row flex-middle">
            <div class="label">
              {{ $t("trading.当前交易额") }}:
              <el-popover
                placement="right"
                :width="200"
                trigger="hover"
                :content="
                  $t('trading.当前该池子的交易额，即该池子暂未提取奖励的交易额')
                "
              >
                <template #reference><span class="icon-pop"></span></template>
              </el-popover>
            </div>
            <div class="flex-item text-r cont">
              {{ Number(item.usdtTotal).toFixed(3) }} USDT
            </div>
          </div>
