            </div>
            <div class="flex-item text-r cont">
              {{ Number(item.userTotal).toFixed(2) }}USDT
            </div>
          </div>
          <div class="row flex-middle">
            <div class="label">
              {{ $t("trading.个人奖励") }}:
              <el-popover
                placement="right"
                :width="200"
                trigger="hover"
                :content="
                  $t(
                    'trading.个人交易额/当前交易额，根据你占池子的份额，增加或减少'
                  )
                "
              >
