      <div class="btn" @click="$router.push({ name: 'Vote' })">
        {{ $t("vote.申请加入") }}
      </div>
    </div>
  </div>
</template>
<script>
import IndexTop from "@/views/index/IndexTop";
import { reactive, toRefs, computed, watch } from "vue";
import swapAbi from "@/utils/swapAbi";
import { useStore } from "vuex";
import swapList from "@/utils/swapList";
import config from "@/utils/config";
import BigNumber from "bignumber.js";
import tokenAbi from "@/utils/abi";
export default {
  components: {
    IndexTop
  },
  setup() {
    const store = useStore();
    const data = reactive({
      themeMode: computed(() => store.state.themeMode),
      poolPairList: swapList,
      rate: computed(() => store.state.rate),
      web3: computed(() => store.state.provider),
      poolTotalLock: 0,
      totalAllocPoint: 0,
      totalMintDay: 0,
      totalMint: 0,
      totalKis: [],
      userTotal: [],
      totalKisAmount: computed(() => {
        let amount = 0;
        data.totalKis.forEach(item => {
          amount = new BigNumber(amount).plus(item);
        });
        return amount.toFixed(5);
      }),
      userTotalAmount: computed(() => {
        let amount = 0;
        data.userTotal.forEach(item => {
          amount += Number(item);
        });
        return amount;
      }),
      address: computed(() => store.state.accounts[0]),
      poolAddress: config.swapMingAddress,
      isLp: true,
      toggleLP: () => {
        data.isLp = !data.isLp;
      },
      getPoolReward: () => {
        let poolContract = new data.web3.eth.Contract(
          swapAbi,
          data.poolAddress
        );
        poolContract.methods
          .totalAllocPoint()
          .call()
          .then(result => {
            data.totalAllocPoint = result;
            console.log("totalAllocPoint", result);
          });
        data.web3.eth.getBlockNumber().then(block => {
          poolContract.methods
            .getKisReward(block)
            .call()
            .then(async result => {
              let startBlock = await poolContract.methods.startBlock().call();
              data.totalMint =
                ((block - startBlock) * result) / Math.pow(10, 18);
              data.totalMintDay = (result * 28800) / Math.pow(10, 18);
            });
        });
      },
      getPoolInfo: () => {
        data.totalKis = [];
        data.userTotal = [];
        let poolContract = new data.web3.eth.Contract(
          swapAbi,
          data.poolAddress
        );
        data.poolPairList.forEach(async item => {
          let poolInfo = await poolContract.methods
            .getPoolInfo(item.pid)
            .call();
          let userReword = await poolContract.methods
            .getUserReward(item.pid)
            .call({ from: data.address });
          item.usdtTotal = Number(poolInfo[3] / Math.pow(10, 18)).toFixed(18);
          item.kisTotal = Number(poolInfo[2] / Math.pow(10, 18)).toFixed(18);
          item.userTotal = Number(userReword[1] / Math.pow(10, 18)).toFixed(18);
          item.userReward = Number(userReword[0] / Math.pow(10, 18)).toFixed(
            18
          );
          data.totalKis.push(item.kisTotal);
          data.userTotal.push(item.userReward);
        });
      },
      takerWithdraw: async () => {
        let poolContract = new data.web3.eth.Contract(
          swapAbi,
          data.poolAddress
        );
        await poolContract.methods.takerWithdraw().send({ from: data.address });
        data.getPoolReward();
        data.getPoolInfo();
      },
      toSwap: item => {
        if (item.tokenB == config.woktAddress) {
          item.tokenB = "OKT";
        }
        if (item.tokenA == config.woktAddress) {
          item.tokenA = "OKT";
        }
        window.location.href = `https://kiswap007.github.io/KISwap/app2/#/swap?inputCurrency=${item.tokenA}&outputCurrency=${item.tokenB}`;
      }
    });
    if (data.address) {
      data.getPoolReward();
      data.getPoolInfo();
    }
    watch(
      () => data.address,
      val => {
        if (val) {
          data.getPoolReward();
          data.getPoolInfo();
        }
      }
    );
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
